import React, { Component, PropTypes } from 'react';
import { OverlayView } from 'react-google-maps';
import { hexbin } from 'd3-hexbin';
import { scaleLinear } from 'd3-scale';
import { interpolateLab } from 'd3-interpolate';
import { max } from 'd3-array';
import Hexagon from './Hexagon.js';
const google = window.google;

export default class Hexbin extends Component {
  constructor(props) {
    super(props);

    // method binding
    this.getLatLng = this.getLatLng.bind(this);
    this.getPoint = this.getPoint.bind(this);
    this.computeHexPointRadius = this.computeHexPointRadius.bind(this);
    this.handleZoomChange = this.handleZoomChange.bind(this);
    this.handleBoundsChange = this.handleBoundsChange.bind(this);
    this.handleProjectionChange = this.handleProjectionChange.bind(this);
    this.createColorScale = this.createColorScale.bind(this);
    this.createHexagons = this.createHexagons.bind(this);
    this.createHexbinGenerator = this.createHexbinGenerator.bind(this);
    
    this.mapRef = this.props.mapHolderRef.getMap();
    this.mapBoundsListener = this.mapRef.addListener('bounds_changed', this.handleBoundsChange);
    this.mapZoomListener = this.mapRef.addListener('zoom_changed', this.handleZoomChange);
    this.mapProjectionListener = this.mapRef.addListener('projection_changed', this.handleProjectionChange)

    this.state = {
      currentZoom: this.mapRef.getZoom(),
      currentBounds: null,
      currentProjection: null,
    };
  }

  componentWillUnmount() {
    google.maps.event.removeListener(this.mapBoundsListener);
    google.maps.event.removeListener(this.mapZoomListener);
    google.maps.event.removeListener(this.mapProjectionListener);
  }

  getPoint(latLng) {
    return (typeof latLng.lat === 'function') ? this.state.currentProjection.fromLatLngToPoint(latLng) : this.state.currentProjection.fromLatLngToPoint(new google.maps.LatLng(latLng));
  }

  getLatLng(point) {
    return this.state.currentProjection.fromPointToLatLng(new google.maps.Point(point.x, point.y));
  }

  computeHexPointRadius() {
    // delta point / delta pixel
    let bottom = this.getPoint(this.state.currentBounds.getSouthWest()).y;
    let top = this.getPoint(this.state.currentBounds.getNorthEast()).y;

    return (bottom - top) * this.props.hexPixelRadius / this.props.mapPixelHeight;
  }

  handleBoundsChange() {
    this.setState({
      currentBounds: this.mapRef.getBounds(),
    });
  }

  handleZoomChange() {
    this.setState({
      currentZoom: this.mapRef.getZoom(),
      currentBounds: this.mapRef.getBounds(),
    });
  }

  handleProjectionChange() {
    this.setState({
      currentBounds: this.mapRef.getBounds(),
      currentProjection: this.mapRef.getProjection()
    })
  }

  createColorScale(hexagons) {
    let maxValue = max(hexagons.map(hexagon => hexagon.weight));
    return scaleLinear()
      .domain([0, 1.0/8.0 * maxValue, 2.0/8.0 * maxValue, 3.0/8.0 * maxValue, 4.0/8.0 * maxValue, 5.0/8.0 * maxValue, 6.0/8.0 * maxValue, 7.0/8.0 * maxValue, maxValue])
      // .range(this.props.colorRange)
      .range(["#2c7bb6","#00a6ca","#00ccbc","#90eb9d","#ffff8c","#f9d057","#f29e2e","#e76818","#d7191c"])
      .interpolate(interpolateLab);
      // https://www.visualcinnamon.com/2016/05/smooth-color-legend-d3-svg-gradient.html
  }

  createHexbinGenerator(hexPointRadius) {
    return hexbin().radius(hexPointRadius);
  }

  createHexagons() {
    // if data is unavailable, return an empty array
    if (!this.props.data) {
      return [];
    }

    let hexagons;
    // declare a new hexbin generator
    let hexbinGenerator;

    // make new hexbin according to new hexPointRadius
    const hexPointRadius = this.computeHexPointRadius();
    hexbinGenerator = this.createHexbinGenerator(hexPointRadius);

    // set x and y accessors
    hexbinGenerator.x(d => d.x);
    hexbinGenerator.y(d => d.y);

    // calculate the hexagons
    hexagons = hexbinGenerator(this.props.data.map(v => {
      let t = this.getPoint(v);
      return {...t, weight: v.count || v.weight}
    }));
    return hexagons.map((hexagon, idx) => { hexagon.id = idx; return hexagon }); // in order to give unique keys
  }

  render() {
    let hexagons = [];
    let colorScale;

    if (this.state.currentProjection) {
      hexagons = this.createHexagons();
      hexagons.forEach(x => {
        x.weight = Math.sqrt(x.reduce((acc, v) => acc + v.weight, 0));
        x.label = x.reduce((acc, v) => acc + v.weight, 0);
        if (x.label > 1000000) {
          x.label = Math.ceil(x.label / 1000000.0) + 'M';
        } else if (x.label > 1000) {
          x.label = Math.ceil(x.label / 1000.0) + 'K';
        }
      })
      colorScale = this.createColorScale(hexagons);
    }

    return (
      <div>
        {
          hexagons
            .filter(hexagon => this.state.currentBounds.contains(this.getLatLng(hexagon)))
            .map(hexagon => {
              return (
                <OverlayView
                  mapHolderRef={this.props.mapHolderRef}
                  position={this.getLatLng(hexagon)}
                  mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
                  key={hexagon.id}
                >
                  <Hexagon
                    hexPixelRadius={this.props.hexPixelRadius}
                    fillColor={colorScale(hexagon.weight)}
                    content={hexagon.label}
                  />
                </OverlayView>
              );
            })
        }
      </div>
    );
  }
}

Hexbin.propTypes = {
  colorRange: PropTypes.array,
  mapHolderRef: PropTypes.object,
  data: PropTypes.array,
  hexPixelRadius: PropTypes.number,
  mapPixelHeight: PropTypes.number,
};



