import React, { Component } from 'react';
import { GoogleMapLoader, GoogleMap } from 'react-google-maps';
import Hexbin from './Hexbin.js';
import './App.css';

const HEX_PIXEL_RADIUS = 30;
const points = window.points;

class App extends Component {
  constructor() {
    super();
    let app = this;
    app.state = {
      height: window.innerHeight
    };
    window.onresize = function(e) {
      app.setState({
        height: window.innerHeight
      })
    }
  }

  render() {
    return (
      <div className="App">
        <div className="sidebar">
        </div>
        <GoogleMapLoader
          query={{ libraries: 'geometry,drawing,places,visualization' }}
          containerElement={
            <div
              style={{ width: '100%', height: this.state.height }}
            />
          }
          googleMapElement={
            <GoogleMap
              defaultZoom={15}
              options={{ mapTypeControl: false }}
              // defaultCenter={{ lat: 37.558397, lng: 127.0 }}
              defaultCenter={{ lat: 37.508791, lng: 127.038439 }}
            >
              <Hexbin
                hexPixelRadius={HEX_PIXEL_RADIUS}
                mapPixelHeight={this.state.height}
                data={points}
                colorRange={['rgb(55, 56, 139)', 'rgb(129, 33, 51)']}
              />
            </GoogleMap>
          }
        />
      </div>
    );
  }
}

export default App;



